'use client'
import React, { useEffect, useState } from 'react'

interface Item {
  id: string
  name: string
}

const ItemPage = () => {
  const [items, setItems] = useState<Item[]>([])
  const [newItemName, setNewItemName] = useState('')
  const [updatedItemName, setUpdatedItemName] = useState('')
  const [editingItemId, setEditingItemId] = useState<string | null>(null)

  const fetchItems = async () => {
    const token = localStorage.getItem('token')
    const res = await fetch('http://localhost:5000/items', {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
    if (res.ok) {
      const data = await res.json()
      setItems(data)
    } else {
      alert('Unauthorized or error fetching items.')
    }
  }

  const addItem = async () => {
    
    if (!newItemName.trim()) {
      alert('Item name cannot be empty.');
      return; 
    }
    
    const token = localStorage.getItem('token')
    const res = await fetch('http://localhost:5000/items', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({ name: newItemName }),
    })

    if (res.ok) {
      setNewItemName('')
      fetchItems()
    } else {
      alert('Failed to add item.')
    }
  }

  const deleteItem = async (id: string) => {
    const token = localStorage.getItem('token')

    const res = await fetch(`http://localhost:5000/items/${id}`, {
      method: 'DELETE',
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })

    if (res.ok) {
      fetchItems()
    } else {
      alert('Failed to delete item.')
    }
  }

  const updateItem = async (id: string) => {
    const token = localStorage.getItem('token')

    const res = await fetch(`http://localhost:5000/items/${id}`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({ name: updatedItemName }),
    })

    if (res.ok) {
      fetchItems()
      setUpdatedItemName('')
      setEditingItemId(null)  
    } else {
      alert('Failed to update item.')
    }
  }

  useEffect(() => {
    fetchItems()
  }, [])

  return (
    <div>
      <h1>Item List</h1>
      <input
        value={newItemName}
        onChange={(e) => setNewItemName(e.target.value)}
        placeholder="New item name"
      />
      <button onClick={addItem}>Add Item</button>
      <ul>
        {items.map((item) => (
          <li key={item.id}>
            {item.name} &nbsp;
            <button onClick={() => deleteItem(item.id)}>Delete</button>
            <button onClick={() => setEditingItemId(item.id)}>Update</button>

            {editingItemId === item.id && (
              <div>
                <input
                  value={updatedItemName}
                  onChange={(e) => setUpdatedItemName(e.target.value)}
                  placeholder="Updated item name"
                />
                <button onClick={() => updateItem(item.id)}>Save</button>
                <button onClick={() => setEditingItemId(null)}>Cancel</button>
              </div>
            )}
          </li>
        ))}
      </ul>
    </div>
  )
}

export default ItemPage
